import "swiper/css";
import "swiper/css/effect-cards";

import React, { useState } from "react";
import {
  Box,
  Button,
  Container,
  Flex,
  FormControl,
  FormErrorMessage,
  Heading,
  Image,
  Input,
  Stack,
  Stat,
  StatArrow,
  StatHelpText,
  StatLabel,
  StatNumber,
  Tag,
  Text,
  useBreakpointValue,
  useInterval,
  useToast,
  VStack,
} from "@chakra-ui/react";
import azuki from "../assets/azuki.png";
import bayc from "../assets/bayc.png";
import dick from "../assets/dickbutts.png";
import punks from "../assets/punks.jpeg";

import emailjs from "@emailjs/browser";
import {
  EMAILJS_PUBLIC_KEY,
  EMAILJS_SERVICE_CODE,
  EMAILJS_TEMPLATE_CODE,
} from "../constants";
import { motion, Variants } from "framer-motion";

import { Swiper, SwiperSlide } from "swiper/react";
import { EffectCards } from "swiper";

const getRandomFloat = (min: number, max: number): number => {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.random() * (max - min + 1) + min;
};

const computePriceChange = (p: number, base = 100): number => {
  return ((p - base) / base) * 100;
};

const variants: { [key: string]: Variants } = {
  container: {
    initial: {},
    animate: {
      transition: { staggerChildren: 0.09, delayChildren: 0.2 },
    },
  },
  item: {
    initial: {
      opacity: 0,
      y: 20,
    },
    animate: {
      opacity: 1,
      y: 0,
    },
  },
  phone: {
    initial: {
      opacity: 0,
      x: 100,
    },
    animate: {
      opacity: 1,
      x: 0,
      transition: { delay: 0.8, duration: 0.8 },
    },
  },
};

const LandingV2 = (): React.ReactElement => {
  const [emailError, setEmailError] = React.useState(false);
  const [email, setEmail] = React.useState("");
  const [loading, setLoading] = React.useState(false);
  const toast = useToast();

  const [mockPrice, updateMockPrice] = useState(getRandomFloat(90, 130));
  const [mockPriceChange, updateMockPriceChange] = useState(
    computePriceChange(mockPrice)
  );

  // update mock price
  useInterval(() => {
    const p = getRandomFloat(80, 180);
    updateMockPriceChange(computePriceChange(p));
    updateMockPrice(p);
  }, 15000);

  React.useEffect(() => {
    if (
      email &&
      !/^[a-zA-Z0-9.! #$%&'*+/=? ^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
        email
      )
    ) {
      setEmailError(true);
    } else {
      setEmailError(false);
    }
  }, [email]);

  const sendEmail = React.useCallback(() => {
    if (!emailError && email) {
      setLoading(true);
      emailjs
        .send(
          EMAILJS_SERVICE_CODE,
          EMAILJS_TEMPLATE_CODE,
          {
            message: "Signed up for notification of Parsa Launch",
            from_email: email,
          },
          EMAILJS_PUBLIC_KEY
        )
        .then(() =>
          toast({
            status: "success",
            title: "We have received your sign-up!",
            description: "You will be notified of when we launch our service.",
          })
        )
        .catch((e) => {
          console.debug(e);
          toast({
            status: "error",
            title: "Sign-up failed.",
            description: "Please try again.",
          });
        })
        .finally(() => {
          setLoading(false);
          setEmail("");
        });
    }
  }, [email, emailError, toast]);

  const width = useBreakpointValue({
    base: "330px",
    md: "350px",
    lg: "450px",
    xl: "500px",
    "2xl": "600px",
  });

  return (
    <Box minH="100vh" overflow="hidden" p={5}>
      <Flex
        flexDir={{ base: "column", lg: "row" }}
        justifyContent={{ base: "flex-start", lg: undefined }}
        alignItems="center"
        gap={{ base: "30px", lg: "150px" }}
      >
        <VStack
          as={motion.div}
          initial="initial"
          animate="animate"
          variants={variants.container}
          pl={{ base: 0, lg: "11vw" }}
          alignItems={{ base: "center", lg: "self-start" }}
          spacing={15}
        >
          <Box py="8vh" as={motion.div} variants={variants.item}>
            <Flex align="center" gap={2}>
              <Heading
                textAlign={{ base: "center", lg: "left" }}
                fontSize={{ base: "34px", md: "43px", lg: "50px" }}
                color="whiteAlpha.700"
              >
                parsa labs
              </Heading>
              <Tag size="lg" colorScheme="purple">
                NFT
              </Tag>
            </Flex>
          </Box>
          <VStack alignItems={{ base: "center", lg: "self-start" }} spacing={8}>
            <Box
              maxW={{ base: "330px", md: "600px" }}
              as={motion.div}
              variants={variants.item}
            >
              <Heading
                textAlign={{ base: "center", lg: "left" }}
                fontSize={{ base: "40px", md: "50px", lg: "65px" }}
                color="whiteAlpha.800"
              >
                NFTs as Index Funds.
              </Heading>
            </Box>
            <Box
              maxW={{ base: "320px", md: "500px" }}
              as={motion.div}
              variants={variants.item}
            >
              <Text
                textAlign={{ base: "center", lg: "left" }}
                fontSize={{ base: "15px", md: "18px", lg: "21px" }}
                color="whiteAlpha.800"
              >
                We provide a diversified portfolio of the top NFTs in the market
                through our innovative NFT index funds, which offer long or
                short exposure to the underlying NFTs. Join us today and be part
                of the future of crypto, powered by cutting-edge parsa labs
                technology.
              </Text>
            </Box>
            <VStack
              alignItems={{ base: "center", lg: "self-start" }}
              spacing={4}
              maxW={{ base: "300px", sm: "320px", md: "420px" }}
              w="full"
            >
              <FormControl
                isInvalid={emailError}
                as={motion.div}
                variants={variants.item}
              >
                <Input
                  placeholder="Your email"
                  type="email"
                  minH="50px"
                  variant="filled"
                  bg="whiteAlpha.700"
                  color="blackAlpha.800"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  borderRadius={8}
                  _hover={{ bg: "whiteAlpha.600" }}
                  _focus={{ borderColor: "whiteAlpha.600" }}
                  _focusVisible={{
                    border: "2px solid",
                    borderColor: emailError ? "red.300" : "whiteAlpha.600",
                  }}
                  _placeholder={{ color: "blackAlpha.500" }}
                />
                {emailError && (
                  <FormErrorMessage>
                    Not a valid email address.
                  </FormErrorMessage>
                )}
              </FormControl>
              <Button
                as={motion.div}
                variants={variants.item}
                w="full"
                bg="blackAlpha.700"
                minH="50px"
                color="whiteAlpha.900"
                variant="solid"
                _hover={{
                  bg: "blackAlpha.800",
                  _disabled: { bg: "blackAlpha.700" },
                }}
                _active={{
                  bg: "blackAlpha.900",
                  _disabled: { bg: "blackAlpha.700" },
                }}
                isDisabled={emailError}
                isLoading={loading}
                onClick={sendEmail}
              >
                Get Notified
              </Button>
              <Box pt="5px" as={motion.div} variants={variants.item}>
                <Text
                  textAlign={{ base: "center", lg: "left" }}
                  whiteSpace={{ base: "normal", md: "nowrap" }}
                  color="whiteAlpha.600"
                  fontSize={{ base: "14px", md: "16px", lg: "18px" }}
                >
                  Get notified of when we launch.
                </Text>
              </Box>
            </VStack>
          </VStack>
        </VStack>
        <Stack align="center">
          <Swiper
            effect="cards"
            grabCursor={true}
            modules={[EffectCards]}
            style={{ height: "100%", width, margin: 0, padding: "50px" }}
          >
            <SwiperSlide>
              <Image src={azuki} />
            </SwiperSlide>
            <SwiperSlide>
              <Image src={dick} />
            </SwiperSlide>
            <SwiperSlide>
              <Image src={bayc} />
            </SwiperSlide>
            <SwiperSlide>
              <Image src={punks} />
            </SwiperSlide>
            <SwiperSlide>
              <Image src={azuki} />
            </SwiperSlide>
            <SwiperSlide>
              <Image src={dick} />
            </SwiperSlide>
            <SwiperSlide>
              <Image src={bayc} />
            </SwiperSlide>
            <SwiperSlide>
              <Image src={punks} />
            </SwiperSlide>
          </Swiper>
          <Box px={5} py={4} borderRadius="xl" bg="blackAlpha.300">
            <Container maxW="xl">
              <Flex justify="space-between" gap={10}>
                <Stat>
                  <StatLabel whiteSpace="nowrap">Price</StatLabel>
                  <StatNumber>${mockPrice.toFixed(2)}</StatNumber>
                  <StatHelpText>
                    <StatArrow
                      type={mockPriceChange > 0 ? "increase" : "decrease"}
                    />
                    {mockPriceChange.toFixed(2)}%
                  </StatHelpText>
                </Stat>
                <Stack>
                  <Heading fontSize="2xl">DaVinci Index</Heading>
                  <Text fontSize="sm" color="gray.400">
                    NFT index comprised of Azuki, Crypto DickButts, Bored Apes,
                    and Punks NFTs. Tracked by DVD index token.
                  </Text>
                </Stack>
              </Flex>
            </Container>
          </Box>
        </Stack>
      </Flex>
    </Box>
  );
};

export default LandingV2;
